@import "base/normalize";
@import "base/fonts";
@import "base/variables";
@import "base/mixins";

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	cursor: pointer;

}

ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	color: white;
}

h1 {
	font-weight: $extra-font-weight;
	font-size: $big-font-size;
	color: $primary;
	font-family: "OpenSans-ExtraBold";
	margin: unset;

		&:before {
		display: block;                
	    width:100%;                    
	    content: '';               
	    position: absolute;              
	    border-bottom: 1px solid #fff;   
	    top: calc(5% - -120px);                       
	    right: 105%;  
	}
}

button {
	font-size: $small-font-size;
	font-weight: $semi-bold-font-weight;
	color: $primary;
	background: transparent;
	text-transform: uppercase;
	border-color: $primary;
	padding:  5px 10px;
	margin-top: 30px;
	border-style: solid;
}

span {
	font-size: $primary-font-size;
	color: $white;
	font-weight: $light-font-weight;
	font-family: "OpenSans-Light";
}

h3 {
	color: white;
	font-weight: $light-font-weight;
	font-family: "OpenSans-Light";
}

section {

    &:after {
    	display: block;                
	    width:80px;                    
	    content: '';               
	    position: absolute;              
	    border-bottom: 1px solid #fff;   
	    top:50%;                         
	    right:0%; 
    }
}

.logo {
	justify-content: flex-start;
		img {
			width: 50px;
			height: 50px;
	}
}


.dark-bg-cl {
	box-shadow: inset 4px 14px 104px 450px rgba(1,3,12,0.9);

}

.scrolldown {
	color: white;
    position: absolute;
    bottom: 50px;
    left: 50%;
    opacity: .7;

    span {
    	position: absolute;
    	left: calc(50% - 12px);
    	width: 25px;
    	height: 25px;
    	border-bottom: 2px solid #fff;
    	border-right: 2px solid #fff;
    	transform: rotate(45deg);
    	animation: animate 1s linear infinite;
    	transition: translate(15px 15px);
    }

    p {
    	position: absolute;
    	margin: -15px -42px;
    	min-width: 83px;
    }

    @keyframes animate {
    	0% {
    		top: -5px;
    		opacity: 0;
    	}
    	25% {
    		top: 0px;
    		opacity: 1;
    	}
    	50%,100% {
    		top: 5px;
    		opacity: 0;
    	}
    }

}
footer {
	bottom: 0;
	right: 100px;
	position: absolute;

	.social {
		position: fixed;
		right: 80px;
		bottom: 20px;

		img {
			max-width: 20px;
			max-height: 20px;
			filter: invert(1);
			margin-left: 10px;
		}
	}

}


.idea {
	@extend %flex-center;
	color: $primary;
    padding-top: 30px;

	img {
		height: 54px;
		width: 54px;
		transform: rotate(-45deg);
		border-color: $primary;
	}

	span {
		color:$primary;
		font-size: $small-font-size;
		text-transform: uppercase;
		font-family: "OpenSans-ExtraBold";
	}
}

.container {
	margin: 0 auto;
	padding: 0 20px;
	width: 100%;	
}



@import "layouts/homepage";
@import "layouts/story";
@import "layouts/projects";
@import "layouts/information";
@import "layouts/media-queries";






